import React, { useState } from 'react';
import barf from './barf.css'

const CatCalorieCalculator = () => {
  const [weight, setWeight] = useState('');
  const [age, setAge] = useState('adult');
  const [activityLevel, setActivityLevel] = useState('active');
  const [meatOption, setMeatOption] = useState('none');
  const [animalGender, setAnimalGender] = useState('cat');
  const [calories, setCalories] = useState(null);
  const [foodRatio, setFoodRatio] = useState(null);

  //      
  const calculateCalories = () => {
    if (!weight || meatOption === 'none') {
      return;
    }

    let calorieAmount;
    if (age === 'kitty') {
      calorieAmount = 40 * weight; //     
    } else if (age === 'adult') {
      calorieAmount = 30 * weight; //      
    } else if (age === 'senior') {
      calorieAmount = 25 * weight; //      
    }

    if (activityLevel === 'lazy') {
      calorieAmount *= 0.95; //      
    }

    if (animalGender === 'female') {
      calorieAmount *= 0.97; //      3%
    }

    let meatRatio, boneRatio, liverRatio, veggiesRatio, fruitsRatio;

    switch (meatOption) {
      case 'chicken':
        meatRatio = 0.75;
        boneRatio = 0.3;
        liverRatio = 0.15;
        veggiesRatio = 0.5;
        fruitsRatio = 0.2;
        break;
      case 'lamb':
        meatRatio = 0.72;
        boneRatio = 0.5;
        liverRatio = 0.15;
        veggiesRatio = 0.5;
        fruitsRatio = 0.3;
        break;
      default:
        return;
    }

    const meatAmount = Math.round(calorieAmount * meatRatio);
    //  '  
    const boneAmount = Math.round(calorieAmount * boneRatio);
    //    
    const liverAmount = Math.round(calorieAmount * liverRatio);
    //    
    const veggiesAmount = Math.round(calorieAmount * veggiesRatio); //    
    const fruitsAmount = Math.round(calorieAmount * fruitsRatio);
    //    

    const foodRatio = {
      meat: meatAmount,
      bone: boneAmount,
      liver: liverAmount,
      veggies: veggiesAmount,
      fruits: fruitsAmount
    };

    setCalories(calorieAmount); //   
    setFoodRatio(foodRatio); //  
  };

  return (
    <div className='conteiner'>
      <h2>    </h2>

      <div>
        <label htmlFor="weight">  ( ):</label>
        <input
          type="number"
          id="weight"
          value={weight}
          onChange={(e) => setWeight(e.target.value)}
        />
      </div>

      <div>
        <label htmlFor="age"> :</label>
        <select id="age" value={age} onChange={(e) => setAge(e.target.value)}>
          <option value="kitty"></option>
          <option value="adult"> </option>
          <option value="senior"> </option>
        </select>
      </div>

      <div>
        <label htmlFor="activityLevel"> :</label>
        <select
          id="activityLevel"
          value={activityLevel}
          onChange={(e) => setActivityLevel(e.target.value)}
        >
          <option value="active"></option>
          <option value="lazy"></option>
        </select>
      </div>

      <div>
        <label htmlFor="meatOption">':</label>
        <select
          id="meatOption"
          value={meatOption}
          onChange={(e) => setMeatOption(e.target.value)}
        >
          <option value="none"> '</option>
          <option value="chicken"> </option>
          <option value="lamb"> </option>
        </select>
      </div>

      <div>
        <label htmlFor="animalGender"> :</label>
        <select
          id="animalGender"
          value={animalGender}
          onChange={(e) => setAnimalGender(e.target.value)}
        >
          <option value="cat"></option>
          <option value="female"></option>
        </select>
      </div>

      <button className='buttonCat' onClick={calculateCalories}></button>

      {calories && (
        <div className="results" id='cat'>
          <h3>:</h3>
          <p>    : {calories}</p>
          {foodRatio && (
            <div>
              <p> '  : {foodRatio.meat.toFixed(2)}</p>
              <p>   : {foodRatio.bone.toFixed(2)}</p>
              <p>   : {foodRatio.liver.toFixed(2)}</p>
              <p>   : {foodRatio.veggies.toFixed(2)}</p>
              <p>   : {foodRatio.fruits.toFixed(2)}</p>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default CatCalorieCalculator;
